.container {
    max-width: 430px;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    position: relative;
    flex-direction: column;
    cursor: default;
}

@media screen and (max-width:600px) {
    .container {
        max-width: 282px;
    }
}