.header__link {
    color: #FFFFFF;
    font-size: 18px;
    line-height: 22px;
    text-decoration: none;
    margin-left: 24px;
    margin-top: 6px;
}

.header__link:hover {
    opacity: 0.5;
}

@media screen and (max-width: 950px) {
    .header__link {
        margin-right: 34px;
        margin-right: 34px;
        font-size: 14px;
        line-height: 17px;
    }
}